import React, { useState, useEffect } from "react";

import MainHeader from "../../components/Users/MainHeader";
import ModuleSideNav from "../../components/Users/ModuleSideNav";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  SectionWrapper,
  Wrapper,
} from "../../components/Styles/MainHeadStyles.js";
import {
  DataRoomSection,
  FormProfile,
} from "../../components/Styles/DataRoomStyle.js";
import "react-big-calendar/lib/css/react-big-calendar.css";
import axios from "axios";
import { loadStripe } from "@stripe/stripe-js";

import { useNavigate } from "react-router-dom";
// Publishable key from Stripe dashboard
const stripePromise = loadStripe(
  "pk_test_51RUJzWAx6rm2q3pys9SgKUPRxNxPZ4P1X6EazNQvnPuHKOOfzGsbylaTLUktId9ANHULkwBk67jnp5aqZ9Dlm6PR00jKdDwvSq"
);

export default function ProfileSettings() {
  const storedUsername = localStorage.getItem("UserLoginData");
  const userLogin = JSON.parse(storedUsername);
  var apiURL = "https://blueprintcatalyst.com/api/user/";
  document.title = "Profile";
  const [errr, seterrr] = useState(false);
  const [dangerMessage, setdangerMessage] = useState("");
  const [companydata, setcompanydata] = useState("");
  const navigate = useNavigate();
  useEffect(() => {
    getcompanydetail();
  }, []);
  const getcompanydetail = async () => {
    let formdata = {
      user_id: userLogin.id,
    };
    try {
      const res = await axios.post(apiURL + "getcompanydetail", formdata, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      setcompanydata(res.data.results[0]);
    } catch (err) {}
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    let formData = {
      company_name: e.target.company_name.value,
      phone: e.target.phone.value,
      company_linkedin: e.target.company_linkedin.value,
      id: companydata.id,
    };

    try {
      const respo = await axios.post(`${apiURL}companydataUpdate`, formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      setdangerMessage("Updated successfully");
      getcompanydetail();
      setTimeout(() => {
        setdangerMessage("");
      }, 2500);
    } catch (err) {}
  };
  return (
    <>
      <Wrapper>
        <div className="fullpage d-block">
          <MainHeader />

          <SectionWrapper className="d-block py-5">
            <div className="container-lg">
              <div className="row">
                <div className="col-md-3">
                  <ModuleSideNav />
                </div>

                <div className="col-md-9">
                  {dangerMessage && (
                    <p
                      className={errr ? " mt-3 error_pop" : "success_pop mt-3"}
                    >
                      {dangerMessage}
                    </p>
                  )}
                  <DataRoomSection className="d-flex flex-column gap-4">
                    <div className="titleroom d-flex  justify-content-between align-items-center text-center">
                      <h4>Profile Settings</h4>
                    </div>
                    <FormProfile>
                      <form
                        method="post"
                        action="javascript:void(0)"
                        onSubmit={handleSubmit}
                      >
                        <div className="row gy-4">
                          {/* Name */}
                          <div className="col-md-6">
                            <div className="d-flex flex-column gap-1">
                              <label htmlFor="name">
                                Company Name{" "}
                                <span className="text-danger fs-5">*</span>
                              </label>
                              <input
                                required
                                defaultValue={companydata.company_name}
                                type="text"
                                name="company_name"
                                id="name"
                                className="form-control"
                                placeholder="Enter your name"
                              />
                            </div>
                          </div>

                          {/* Email */}
                          <div className="col-md-6">
                            <div className="d-flex flex-column gap-1">
                              <label htmlFor="email">
                                Email{" "}
                                <span className="text-danger fs-5">*</span>
                              </label>
                              <input
                                disabled
                                defaultValue={companydata.email}
                                type="email"
                                name="email"
                                id="email"
                                className="form-control"
                                placeholder="Enter your email"
                              />
                            </div>
                          </div>

                          {/* Phone */}
                          <div className="col-md-6">
                            <div className="d-flex flex-column gap-1">
                              <label htmlFor="phone">
                                Phone{" "}
                                <span className="text-danger fs-5">*</span>
                              </label>
                              <input
                                defaultValue={companydata.phone}
                                type="tel"
                                name="phone"
                                id="phone"
                                className="form-control"
                                placeholder="Enter phone number"
                              />
                            </div>
                          </div>

                          {/* LinkedIn Profile */}
                          <div className="col-md-6">
                            <div className="d-flex flex-column gap-1">
                              <label htmlFor="linkedin">
                                LinkedIn Profile{" "}
                                <span className="text-danger fs-5">*</span>
                              </label>
                              <input
                                defaultValue={companydata.company_linkedin}
                                type="url"
                                name="company_linkedin"
                                id="linkedin"
                                className="form-control"
                                placeholder="https://linkedin.com/in/yourprofile"
                              />
                            </div>
                          </div>

                          {/* Current Password */}
                          {/* <div className="col-md-6">
                            <div className="d-flex flex-column gap-1">
                              <label htmlFor="currentPassword">
                                Current Password{" "}
                                <span className="text-danger fs-5">*</span>
                              </label>
                              <input
                                type="password"
                                id="currentPassword"
                                className="form-control"
                                placeholder="••••••••"
                              />
                            </div>
                          </div> */}

                          {/* New Password */}
                          {/* <div className="col-md-6">
                            <div className="d-flex flex-column gap-1">
                              <label htmlFor="newPassword">
                                New Password{" "}
                                <span className="text-danger fs-5">*</span>
                              </label>
                              <input
                                type="password"
                                id="newPassword"
                                className="form-control"
                                placeholder="New password"
                              />
                            </div>
                          </div> */}

                          {/* Confirm Password */}
                          {/* <div className="col-md-6">
                            <div className="d-flex flex-column gap-1">
                              <label htmlFor="confirmPassword">
                                Confirm New Password{" "}
                                <span className="text-danger fs-5">*</span>
                              </label>
                              <input
                                type="password"
                                id="confirmPassword"
                                className="form-control"
                                placeholder="Confirm new password"
                              />
                            </div>
                          </div> */}

                          {/* Save Button */}
                          <div className="col-12 text-end mt-3">
                            <button type="submit" className="">
                              Save Changes
                            </button>
                          </div>
                        </div>
                      </form>
                    </FormProfile>
                  </DataRoomSection>
                </div>
              </div>
            </div>
          </SectionWrapper>
        </div>
      </Wrapper>
    </>
  );
}
